.aboutMe {
    width: fit-content;
    margin: auto;
    margin-top: 10rem;
    padding: 1.5rem;
    font-size: 2rem; 
    color: rgb(158, 166, 173);
    border-top: dotted;
    border-color: rgba(240, 248, 255, 0.144);
}
@media (max-width: 985px) {
    .aboutMe {
        max-width: 90%;
    }
}
@media(max-width: 650px) {

}
@media(max-width: 560px) {
    .aboutMe {
        font-size: 1.5rem;
    }
}
@media(max-width: 450px) {
    .aboutMe {
        font-size: 1.2rem;
    }
}