.timeline {
    position: relative;
    width: 100%;
    margin: 0 auto;
    margin-top: 10rem;
    padding-top: 50px;
}
.timeline ul {
    width: 100%;
}
.timeline::after {
    content: '';
    position: absolute;
    width: 6px;
    background-color: white;
    top: 0;
    bottom: 0;
    left: 50%;
    margin-left: -3px;
}

@media (max-width: 985px) {
    .timeline {
        max-width: 90%;
    }
}
@media(max-width: 650px) {
    .timeline::after {
        left: 20px;
    }
}
@media(max-width: 560px) {
    .timeline {
        padding-top: 25px;
    }
}
@media(max-width: 450px) {
    .timeline {
        margin-top: 5rem;
        padding-top: 15px;
    }
}