}
@media(max-width: 560px) {

}
@media(max-width: 450px) {
    footer {
        padding: 1rem;
    }
    footer li {
        padding: 0 1.5rem;
    }
    footer p {
        font-size: 0;
        margin: 0;
    }
    footer img {
        margin-top: 0.25rem;
    }
}